import { CopyOutlined } from "@ant-design/icons";
import { Button, Card, Space, Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import Title from "antd/es/typography/Title";
import copy from 'copy-to-clipboard';
import { useEffect, useState } from "react";

interface DataType {
    key: React.Key;
    filename: string;
    release: string;
    filesize: string;
    time: string;
}

const SourceMapManage = () => {
    const [key, setKey] = useState<string>('');
    const [token, setToken] = useState<string>('');
    useEffect(() => {
        setKey('xxxx-xxxx-xxxx');
        setToken('xxxx-xxxx-xxxx');
    }, []);

    const copyText = (e: React.MouseEvent, text: string) => {
        e.stopPropagation();
        copy(text);
        message.success('已复制')
    }

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [errorTotal] = useState(0);
    const [errorList] = useState<DataType[]>([]);

    const columns: ColumnsType<DataType> = [
        {
            title: '文件名',
            dataIndex: 'filename',
            width: 200,
        },
        {
            title: 'release',
            dataIndex: 'release',
            width: 100,
        },
        {
            title: '文件大小',
            dataIndex: 'filesize',
            width: 50,
        },
        {
            title: '上传时间',
            dataIndex: 'time',
            width: 100,
        },
        {
            title: '操作',
            width: 100,
            render: (_, record) => (
                <Space size="middle">
                    <Button type="link">下载 Sourcemap</Button>
                </Space>
            ),
        },
    ];

    useEffect(() => {

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: errorTotal
    }

    return (<div>
        <div style={{ background: '#ffffff', padding: '12px 16px' }}>
            <Title level={5}>自动上传密钥</Title>
            <p>API Key:{key} <CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, key)} />  API Token:{token} <CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, token)} /></p>
        </div>
        <Card title="sourcemap列表" style={{ marginTop: '20px' }}>
            <Table style={{ marginTop: '12px' }} pagination={pagination} columns={columns} dataSource={errorList} />
        </Card>
    </div>);
}

export default SourceMapManage;